@tailwind base;
@tailwind components;
@tailwind utilities;

html, body {margin: 0; height: 100%; overflow: hidden}

@layer components {
  .page-section {
    @apply w-full h-screen z-50;
  }

	.clickable {
		@apply p-4 border-2 border-transparent rounded-md z-50 ;
	}
}


*::-webkit-scrollbar-track
{
	/* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); */
	background-color: transparent;
	border-radius: 5px;
}

*::-webkit-scrollbar
{
	width: 6px;
	background-color: transparent;
}

*::-webkit-scrollbar-thumb
{
	background-color: rgb(136, 136, 136);
	border-radius: 5px;
}
